import React from "react";
import "./IncomeItem.css";
import CostDate from "./CostDate";
import Card from "./Card";
import { t } from "./localization";
function IncomeItem(props) {
  const deleteHandler = () => {
    if (props.onDelete) {
      props.onDelete(props.id); // Убедитесь, что `onDelete` вызывается с корректным ID
    } else {
      console.error("onDelete function is not provided to IncomeItem.");
    }
  };

  const editHandler = () => {
    if (props.onEdit) {
      props.onEdit(props.id);
     window.scrollTo({
        top: 100, // Укажите необходимую позицию
        behavior: "smooth",
      }); 
       // Убедитесь, что `onEdit` вызывается с корректным ID
    } else {
      console.error("onEdit function is not provided to IncomeItem.");
    }
  };

  return (
    <li>
      <Card className="cost-item">
        <div className="cost-item__left">
          <CostDate date={props.date} language={props.language} />
          <div className="cost-item__description">
            <h3>{props.type}</h3>
          </div>
        </div>
        <div className="cost-item__right">
          <div className="cost-income-item__price">${props.amount}</div>
          <span className="cost-income-item__type">
  {props.entrytype === "piggy bank" ? t("piggybank", props.language) : t("income", props.language)}
</span>
          <div className="cost-item__actions">
            <button className="cost-item__edit" onClick={editHandler}>
              ✎
            </button>
            <button className="cost-item__delete" onClick={deleteHandler}>
              ✖
            </button>
          </div>
        </div>
      </Card>
    </li>
  );
}

export default IncomeItem;
