import { useState } from "react";
import { t } from "../localization"; // Импорт перевода

const IncomeForm = (props) => {
  const [enteredType, setEnteredType] = useState("");
  const [enteredAmount, setEnteredAmount] = useState("");
  const [enteredDate, setEnteredDate] = useState("");
  const [isPiggyBank, setIsPiggyBank] = useState(false); // Чекбокс для "piggy bank"

  const typeChangeHandler = (event) => {
    setEnteredType(event.target.value);
  };

  const amountChangeHandler = (event) => {
    setEnteredAmount(event.target.value);
  };

  const dateChangeHandler = (event) => {
    setEnteredDate(event.target.value);
  };

  const piggyBankChangeHandler = (event) => {
    setIsPiggyBank(event.target.checked);
  };

  const submitHandler = (event) => {
    event.preventDefault();

    // Проверяем, что сумма и дата указаны
    if (!enteredAmount || !enteredDate) {
      alert(t("fillAmountAndDate", props.language)); // Перевод ошибки
      return;
    }

    // Если все поля заполнены, отправляем данные
    const incomeData = {
      type: enteredType,
      amount: +enteredAmount,
      date: enteredDate,
      entrytype: isPiggyBank ? "piggy bank" : "income", // Устанавливаем тип дохода
    };

    props.onSaveIncomeData(incomeData);

    // Очистка полей после отправки данных
    setEnteredType("");
    setEnteredAmount("");
    setEnteredDate("");
    setIsPiggyBank(false);
  };

  return (
    <form onSubmit={submitHandler}>
      <div className="new-income__controls">
        <div className="new-income__control">
          <label>{t("incomeType", props.language)}</label>
          <input
            type="text"
            value={enteredType}
            onChange={typeChangeHandler}
          />
        </div>
        <div className="new-income__control">
          <label>{t("amount", props.language)}</label>
          <input
            type="number"
            value={enteredAmount}
            onChange={amountChangeHandler}
          />
        </div>
        <div className="new-income__control">
          <label>{t("date", props.language)}</label>
          <input
            type="date"
            value={enteredDate}
            onChange={dateChangeHandler}
          />
        </div>
        <div className="new-income__control">
          <label>
            <input
              type="checkbox"
              checked={isPiggyBank}
              onChange={piggyBankChangeHandler}
            />
            {t("piggyBank", props.language)} {/* Перевод текста чекбокса */}
          </label>
        </div>
      </div>
      <div className="new-income__actions">
        <button type="submit">{t("addIncome", props.language)}</button>
        <button type="button" onClick={props.onCancel}>
          {t("cancel", props.language)}
        </button>
      </div>
    </form>
  );
};

export default IncomeForm;
