import React, { useState, useEffect } from "react";
import "./ExchangeRates.css"; // Подключаем стили
import { t } from "./localization";

const ExchangeRates = ({ language }) => {
  const [rates, setRates] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchRates = async () => {
      try {
        const response = await fetch("https://bank.gov.ua/NBUStatService/v1/statdirectory/exchange?json");
        const data = await response.json();

        const usdRate = data.find((item) => item.cc === "USD")?.rate;
        const eurRate = data.find((item) => item.cc === "EUR")?.rate;

        if (usdRate && eurRate) {
          setRates({ USD: usdRate, EUR: eurRate });
        } else {
          throw new Error("Курсы валют не найдены");
        }
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchRates();
  }, []);

  if (loading) return <p>Загружаем курсы валют...</p>;
  if (error) return <p className="error-message">Ошибка: {error}</p>;

  return (
    <div className="exchange-rates">
      <h3>{t("exchangeRates", language)}</h3>
      <ul>
        <li>{t("usd", language)}: {rates.USD.toFixed(2)} {t("uah", language)} {t("eur", language)}: {rates.EUR.toFixed(2)} {t("uah", language)}</li>
      </ul>
    </div>
  );
};

export default ExchangeRates;
