import React from "react";
import {
  PieChart,
  Pie,
  Cell,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { t } from "./localization"; // Импорт функции перевода
import "./PieChartComponent.css";

const PieChartComponent = ({ data, language }) => {
  const COLORS = [
    "#0088FE", // Синий
    "#00C49F", // Зелёный
    "#FFBB28", // Жёлтый
    "#FF8042", // Оранжевый
    "#845EC2", // Фиолетовый
    "#D65DB1", // Розовый
    "#FF6F91", // Красновато-розовый
    "#FFC75F", // Светло-жёлтый
    "#F9F871", // Лимонный
    "#4B4453", // Тёмный серо-фиолетовый
  ];

  // Сумма всех значений для расчёта процентов
  const totalValue = data.reduce((sum, entry) => sum + entry.value, 0);
  const translateCategory = (category, language) => {
    if (category === "food") return t("food", language);
    if (category === "transport") return t("transport", language);
    if (category === "entertainment") return t("entertainment", language);
    if (category === "utilities") return t("utilities", language);
    return category; // Если категория нестандартная, оставляем как есть
  };

  // Локализуем категории перед передачей в график
 const localizedData = data.map((entry) => ({
    ...entry,
    category: translateCategory(entry.category, language),
  }));

  // Функция для сокращения названия категории до первых 3 букв
  const shortenLabel = (category) => {
    return category.length > 3 ? `${category.substring(0, 3)}...` : category;
  };

  return (
    <div className="pie-chart-container">
      <ResponsiveContainer width="100%" height={data.length > 4 ? 400 : 300}>
        <PieChart>
          <Pie
            data={localizedData} // Используем локализованные данные
            dataKey="value"
            nameKey="category"
            cx="50%"
            cy="50%"
            outerRadius={data.length > 4 ? "60%" : "70%"} // Уменьшаем радиус, если категорий много
            fill="#8884d8"
            label={({ category, percent }) =>
              `${shortenLabel(category)}: ${(percent * 100).toFixed(1)}%`
            }
          >
            {localizedData.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip
            formatter={(value, name) => {
              const percentage = ((value / totalValue) * 100).toFixed(1);
              return [`${value} (${percentage}%)`, name];
            }}
          />
          <Legend
            layout="horizontal"
            verticalAlign="bottom"
            align="center"
            wrapperStyle={{
              fontSize: "12px",
              maxWidth: "100%",
              whiteSpace: "normal",
              overflow: "hidden",
              textOverflow: "ellipsis",
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "center",
              paddingTop: data.length > 4 ? "20px" : "0", // Добавляем отступ, если категорий много
            }}
          />
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
};

export default PieChartComponent;
