import React from "react";
import "./CostDate.css";

function CostDate({ date, language }) {
  // Проверяем, является ли дата объектом Date, и преобразуем её при необходимости
  const parsedDate = date instanceof Date ? date : new Date(date);

  // Обрабатываем случаи, когда дата некорректна
  if (isNaN(parsedDate)) {
    return <div className="cost-date">Invalid Date</div>;
  }

  // Локализация даты
  const month = parsedDate.toLocaleString(language, { month: "long" });
  const year = parsedDate.getFullYear();
  const day = parsedDate.toLocaleString(language, { day: "2-digit" });

  return (
    <div className="cost-date">
      <div className="cost-date__month">{month}</div>
      <div className="cost-date__year">{year}</div>
      <div className="cost-date__day">{day}</div>
    </div>
  );
}

export default CostDate;
