import React from "react";
import "./CostItem.css";
import CostDate from "./CostDate";
import Card from "./Card";
import { t } from "./localization";

function CostItem(props) {
  const deleteHandler = () => {
    props.onDelete(props.id);
  };

  const editHandler = () => {
    props.onEdit(props.id);
    window.scrollTo({
      top: 100,
      behavior: "smooth",
    });
  };

  return (
    <li>
      <Card className="cost-item">
        <div className="cost-item__left">
          {/* Используем props.language */}
          <CostDate date={props.date} language={props.language} />
          <div className="cost-item__description">
            <h3>{props.description}</h3>
            <span className="cost-item__category">
  {props.category === "food"
    ? t("food", props.language)
    : props.category === "transport"
    ? t("transport", props.language)
    : props.category === "entertainment"
    ? t("entertainment", props.language)
    : props.category === "utilities"
    ? t("utilities", props.language)
    : props.category} {/* Оставляем оригинал, если не найден перевод */}
</span>
          </div>
        </div>
        <div className="cost-item__right">
          <div className="cost-item__price">${props.price}</div>
          <span className="cost-item__type">{t("expense", props.language)}</span>
          <div className="cost-item__actions">
            <button className="cost-item__edit" onClick={editHandler}>
              ✎
            </button>
            <button className="cost-item__delete" onClick={deleteHandler}>
              ✖
            </button>
          </div>
        </div>
      </Card>
    </li>
  );
}

export default CostItem;
